import React, { useState, useEffect } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import "./Nav.css";
export default function Nav() {
  const [show, setShow] = useState(false);
  const [searchValue, setSearchValue] = useState("");
  const navigate = useNavigate();

  useEffect(() => {
    window.addEventListener("scroll", () => {
      console.log("window.scrollY", window.scrollY);
      if (window.scrollY > 50) {
        setShow(true);
      } else {
        setShow(false);
      }
    });

    return () => {
      window.removeEventListener("scroll", () => {});
    };
  }, []);
  // scroll 내릴때 50보다 크면 setshow를 true로 해서
  // {`nav ${show && "nav_black"}`} show가 true 일때 nav_black css가 나오도록

  const handleChange = (e) => {
    setSearchValue(e.target.value);
    navigate(`/search?q=${e.target.value}`);
  };
  // 🔍 검색할때 💡[navigate]를 이용해서 검색한 target과 같은 것 찾아주는 기능 구현 

 
  
  return (
    <nav className={`nav ${show && "nav_black"}`}>
      <img
        alt="Netflix logo"
        src="data:image/png;base64,"
        className="nav_logo"
        onClick={() => { 
          window.location.reload();
        }}
      />
      <input
        value={searchValue}
        onChange={handleChange}
        className="nav__input"
        type="text"
        placeholder="영화를 검색해주세요."
      />
      {/* 검색 기능을위해 input bar 생성 */}
      <img
        alt="User logged"
        src="https://pbs.twimg.com/profile_images/1356333120992149505/-qvakEK7_400x400.jpg"
        className="nav_avator"
      />
    </nav>
  );
}
